.repair .repair__title {
  padding: 16px;
  height: 100%;
  background: linear-gradient(to bottom, #F8F8F8 50%, #FFFFFF 98.61%); }

.repair {
  padding-bottom: 24px;
  border-bottom: 1px solid #D9FFF5; }

.repair .show-link {
  display: none; }

.swiper--repair {
  margin-top: 16px; }
  .swiper--repair .swiper-slide {
    padding-left: 16px; }

.repair__items {
  display: none; }

.repair__item {
  position: relative;
  width: 240px;
  height: 160px;
  background-color: #FFFFFF;
  border-radius: 6px;
  transition: transform .7s, background 1s; }
  .repair__item:hover {
    cursor: pointer;
    transform: scale(0.98);
    border: 2px solid #FF3E79; }
    .repair__item:hover .link-btn__arrow {
      left: 60%; }
  .repair__item .repair__item-title {
    padding-top: 16px;
    padding-left: 16px;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: #1B1C21; }
  .repair__item .link-btn {
    position: absolute;
    right: 16px;
    bottom: 16px; }

.strange:hover {
  background-color: transparent;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 0 65px;
  background-image: url(./static/fray.png); }

@media (min-width: 560px) {
  .swiper--repair {
    display: none; }
  .repair .show-link {
    display: block;
    margin-top: 24px;
    margin-left: 16px; }
    .repair .show-link:hover {
      cursor: pointer;
      color: #9A1A1A; }
  .repair__items {
    display: flex;
    flex-wrap: wrap;
    height: 170px;
    margin-top: 24px;
    padding-left: 16px;
    overflow-y: hidden; }
    .repair__items .repair__item {
      width: 224px;
      margin-right: 16px; } }

@media (min-width: 768px) {
  .repair .repair__title {
    padding-left: 24px; }
  .repair .show-link {
    margin-left: 24px;
    margin-bottom: 32px; }
  .repair__items {
    padding-left: 24px; }
    .repair__items .repair__item {
      width: 224px;
      margin-right: 24px;
      margin-bottom: 24px; } }

@media (min-width: 1440px) {
  .repair {
    padding-bottom: 0; }
    .repair .repair__title {
      padding-left: 32px; }
    .repair .show-link {
      margin-left: 24px;
      margin-bottom: 50px; }
  .repair__items {
    padding-left: 32px; }
    .repair__items .repair__item {
      width: 240px;
      margin-right: 32px;
      margin-bottom: 24px; } }
