body {
  font-family: 'TTLakes', sans-serif;
  background-color: #F8F8F8; }

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0; }

ul {
  list-style: none; }

.img {
  width: 100%; }

.main-container {
  position: relative;
  max-width: 1440px;
  margin: 0 auto; }

.btn {
  position: relative;
  width: 40px;
  height: 40px;
  background-color: #FF3E79;
  border: none;
  border-radius: 50%;
  transition: transform .5s, background-color .5s; }
  .btn:hover {
    cursor: pointer;
    background-color: #9A1A1A;
    transform: scale(1.05); }
  .btn__svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }

.header__logo-items .callback-icons {
  display: none; }

.subtitle {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  text-transform: uppercase;
  color: #7E7E82; }

input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #B5B6BC; }

input::-moz-placeholder, textarea::-moz-placeholder {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #B5B6BC; }

input:-ms-input-placeholder, textarea:-ms-input-placeholder {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #B5B6BC; }

input::placeholder, textarea::placeholder {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #B5B6BC; }

.swiper-pagination {
  position: static;
  margin-top: 10px; }

.burger {
  position: relative;
  width: 40px;
  height: 40px;
  background-color: #FF3E79;
  border-radius: 50%; }
  .burger::before {
    content: '';
    position: absolute;
    top: 12px;
    left: 11px;
    width: 18px;
    height: 2px;
    background-color: #FFFFFF;
    border-radius: 2px; }
  .burger::after {
    content: '';
    position: absolute;
    top: 19px;
    left: 11px;
    width: 10px;
    height: 2px;
    background-color: #FFFFFF;
    border-radius: 2px; }
  .burger span {
    position: absolute;
    top: 26px;
    left: 11px;
    width: 14px;
    height: 2px;
    background-color: #FFFFFF;
    border-radius: 2px; }
  .burger:hover {
    cursor: pointer; }

.link-btn {
  position: relative;
  width: 40px;
  height: 40px;
  margin-right: 16px; }
  .link-btn .link-btn__circle {
    position: absolute; }
  .link-btn .link-btn__arrow {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: left .3s; }

.button {
  position: relative;
  display: inline-block;
  width: 119px;
  height: 32px;
  background-color: #FF3E79;
  border: none;
  border-radius: 16px;
  font-weight: 600;
  font-size: 12px;
  line-height: 24px;
  letter-spacing: -0.15px;
  text-transform: uppercase;
  color: #FFFFFF;
  transition: background-color .5s, transform .5s; }
  .button button {
    width: 100%;
    height: 100%;
    background-color: transparent;
    border: none; }
    .button button:hover {
      cursor: pointer; }
  .button span {
    position: absolute;
    top: 4px;
    left: 12px; }
  .button-arrow {
    position: absolute;
    top: 11px;
    right: 16px;
    transition: transform .5s; }
  .button:hover {
    cursor: pointer;
    background-color: #9A1A1A;
    transform: scale(1.03); }
    .button:hover .button-arrow {
      transform: translateX(2px); }

.hidden {
  display: none; }

@media (min-width: 1088px) {
  .wrapper {
    margin-left: 320px; }
  .aside .aside__header {
    justify-content: space-between; }
  .aside__btn {
    display: none; }
  .aside {
    transform: translateX(1000px); } }
