.brand .brand__title {
  padding: 16px;
  height: 100%;
  background: linear-gradient(to bottom, #F8F8F8 50%, #FFFFFF 98.61%); }

.brand {
  padding-bottom: 24px;
  border-bottom: 1px solid #D9FFF5; }

.brand .show-link {
  display: none; }

.brand__items {
  display: none; }

.brand .swiper-slide {
  margin-top: 16px;
  padding-left: 16px; }

.brand__item {
  width: 240px;
  height: 72px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #FFFFFF;
  border-radius: 6px;
  transition: transform .7s; }
  .brand__item .brand-logo {
    padding-left: 16px; }
  .brand__item:hover {
    cursor: pointer;
    transform: scale(0.98);
    border: 2px solid #FF3E79; }
    .brand__item:hover .link-btn__arrow {
      left: 60%; }

@media (min-width: 560px) {
  .brand--swiper {
    display: none; }
  .brand .show-link {
    display: block;
    margin-top: 24px;
    margin-bottom: 8px; }
    .brand .show-link:hover {
      cursor: pointer;
      color: #9A1A1A; }
  .brand .show-link {
    margin-left: 16px; }
  .brand__items {
    display: flex;
    flex-wrap: wrap;
    padding-left: 16px;
    margin-top: 24px;
    height: 160px;
    overflow-y: hidden; }
    .brand__items .brand__item {
      margin-right: 24px;
      margin-bottom: 16px; } }

@media (min-width: 768px) {
  .brand {
    padding-bottom: 32px; }
    .brand .brand__title {
      padding-left: 24px; }
    .brand .show-link {
      margin-left: 24px; }
  .brand__items {
    padding-left: 24px; }
    .brand__items .brand__item {
      margin-bottom: 16px;
      width: 224px; } }

@media (min-width: 1020px) {
  .brand {
    padding-bottom: 50px; }
    .brand .brand__title {
      padding-left: 32px; }
    .brand .show-link {
      margin-left: 32px; }
    .brand .brand__items {
      padding-left: 32px; }
      .brand .brand__items .brand__item {
        width: 240px;
        margin-right: 32px;
        margin-bottom: 16px; } }

@media (min-width: 1440px) {
  .brand .brand__items .brand__item {
    width: 240px;
    margin-right: 32px;
    margin-bottom: 16px; } }
