.price .price__title {
  padding: 16px;
  height: 100%;
  background: linear-gradient(to bottom, #F8F8F8 50%, #FFFFFF 98.61%); }

.price {
  padding-bottom: 24px;
  border-bottom: 1px solid #D9FFF5; }

.price .price__title {
  height: 100%; }

.price--swiper .swiper-slide {
  margin-top: 16px;
  padding-left: 8px; }

.price__items {
  display: none; }

.price__item {
  width: 240px;
  height: 200px;
  background-color: #FFFFFF;
  border-radius: 6px; }
  .price__item-wrapper {
    position: relative;
    padding: 16px 0 0 16px;
    margin-right: 16px; }
    .price__item-wrapper .price--button {
      position: absolute;
      bottom: 0;
      right: 0; }

.item-container {
  height: 52px;
  margin-bottom: 16px; }

.price__item-heading {
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  opacity: 0.72;
  color: #7E7E82; }

.price__item-text {
  padding-top: 4px;
  margin-bottom: 16px;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.2px;
  color: #1B1C21; }

.price--button {
  position: absolute;
  bottom: 0;
  right: 0; }

.price__tax-text {
  padding: 16px 16px;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.2px;
  color: #1B1C21; }

.price__commercial {
  width: 220px;
  padding-left: 16px; }
  .price__commercial:hover {
    cursor: pointer; }
  .price__commercial svg {
    margin-left: 5px;
    transform: translateY(1px); }
  .price__commercial .price__commercial-link {
    width: 100%;
    text-decoration: none;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: #1B1C21; }
    .price__commercial .price__commercial-link:hover {
      color: #9A1A1A; }

@media (min-width: 600px) {
  .price__tax {
    width: 421px; }
  .price__tax-text {
    padding-left: 24px; }
  .price__commercial {
    padding-left: 24px;
    width: 100%; } }

@media (min-width: 768px) {
  .price--swiper {
    display: none; }
  .price {
    padding-bottom: 47px; }
    .price .price__title {
      padding-left: 24px; }
  .price__items {
    display: flex;
    flex-flow: column;
    margin: 32px 24px 24px; }
  .price__item {
    display: flex;
    align-items: center;
    width: 100%;
    height: 65px;
    padding-left: 16px;
    padding-right: 16px;
    border-radius: 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.2px;
    color: #1B1C21; }
    .price__item:nth-child(2) {
      border-top-right-radius: 6px;
      border-top-left-radius: 6px; }
    .price__item:last-child {
      border-bottom-right-radius: 6px;
      border-bottom-left-radius: 6px; }
  .price--heading {
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    color: #7E7E82;
    mix-blend-mode: normal;
    opacity: 0.72;
    background-color: #F8F8F8; }
  .price__item-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    width: 100%; }
    .price__item-content span {
      display: inline-block; }
    .price__item-content span:first-child {
      min-width: 348px;
      margin-right: 24px; }
    .price__item-content span:nth-child(2) {
      min-width: 74px;
      margin-right: 22px; }
    .price__item-content span:nth-child(3) {
      min-width: 81px;
      margin-right: 20px; } }

.price__item:not(:first-child):not(:last-child) {
  position: relative; }
  .price__item:not(:first-child):not(:last-child):after {
    content: '';
    width: 97%;
    height: 1px;
    position: absolute;
    left: 15px;
    bottom: 0;
    background-color: #EAEAEA; }

@media (min-width: 1440px) {
  .price {
    padding-bottom: 50px; }
    .price .price__title {
      padding-left: 32px; }
  .price__items {
    margin: 32px 32px; } }
