.modal__heading {
  position: relative; }
  .modal__heading:before {
    content: '';
    position: absolute;
    top: 50%;
    left: -16px;
    transform: translateY(-50%);
    width: 4px;
    height: 40px;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    background-color: #41F6D7; }

.modal {
  position: fixed;
  top: 0;
  right: -200%;
  min-width: 320px;
  width: 100%;
  max-width: 440px;
  height: 100%;
  background-color: #FFFFFF;
  transition: right 0.7s cubic-bezier(0.85, 0, 0.15, 1), transform 0.4s cubic-bezier(0.85, 0, 0.15, 1);
  outline: 1px solid #D9FFF5;
  box-shadow: -10px 0 50px #DDDDDD; }

.show--modal {
  z-index: 3; }

.modal__wrapper {
  padding: 24px 16px 0; }

.modal__heading {
  margin-top: 32px;
  margin-bottom: 32px; }

.form__input {
  height: 48px; }

.form__area {
  height: 118px;
  resize: none; }

.form__input, .form__area {
  width: 100%;
  margin-bottom: 16px;
  padding: 16px;
  border: 1px solid #EAEAEA;
  border-radius: 6px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  color: #7E7E82; }

.form__text {
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  color: #7E7E82; }
  .form__text-link {
    text-decoration: none;
    color: #FF3E79; }
    .form__text-link:hover {
      cursor: pointer; }

.form {
  position: relative; }
  .form .button {
    position: absolute;
    margin-top: 19px;
    right: 0; }

@media (min-width: 768px) {
  .modal__wrapper {
    padding: 40px 32px 0; }
  .modal__btn {
    position: absolute;
    left: -65px; }
  .modal__heading {
    margin-top: 8px;
    margin-bottom: 24px; }
    .modal__heading:before {
      left: -32px; } }
