.hero {
  padding-bottom: 24px;
  border-bottom: 1px solid #D9FFF5; }

.hero__heading {
  position: relative; }
  .hero__heading:before {
    content: '';
    position: absolute;
    top: 50%;
    left: -16px;
    transform: translateY(-50%);
    width: 4px;
    height: 40px;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    background-color: #41F6D7; }

.hero {
  padding: 24px 16px; }

.hero__nav-items {
  display: flex;
  align-items: center;
  overflow: hidden;
  overflow-x: auto;
  margin-top: 32px;
  -ms-overflow-style: none; }
  .hero__nav-items::-webkit-scrollbar {
    display: none; }
  .hero__nav-items .hero__item {
    display: flex;
    align-items: center;
    height: 40px;
    white-space: nowrap; }
    .hero__nav-items .hero__item:not(:last-child) {
      margin-right: 7px; }

.hero__subtitle {
  padding: 8px 15px;
  text-decoration: none;
  font-weight: 500;
  font-size: 16px;
  letter-spacing: -0.2px;
  color: #7E7E82;
  transition: outline-color 1s, background-color 1s; }
  .hero__subtitle:focus {
    outline: 2px solid #B8FFEC;
    border-radius: 6px;
    background-color: #FFFFFF; }
  .hero__subtitle:hover {
    outline: 2px solid #B8FFEC;
    border-radius: 6px; }

.hero__text-container {
  margin-bottom: 16px;
  padding-right: 10px;
  height: 90px;
  overflow: hidden; }
  .hero__text-container .hero__text-content {
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.2px;
    color: #1B1C21; }
    .hero__text-container .hero__text-content:not(:last-child) {
      margin-bottom: 20px; }

.show-link {
  margin-top: 13px;
  padding-left: 16px;
  margin-bottom: 16px;
  background-image: url(./static/down.png);
  background-repeat: no-repeat;
  background-position: center left;
  background-size: 7px 10px; }
  .show-link:hover {
    background-size: 7px 13px; }
  .show-link__text {
    background-color: transparent;
    border: none;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px; }
    .show-link__text:hover {
      cursor: pointer;
      color: #9A1A1A; }

.hero__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.hero__callback-btn {
  display: none; }

.hero__main-wrapper {
  margin-top: 24px; }

.hero__main-logo {
  min-height: 176px;
  height: 40vw;
  background-image: url(./static/cps.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center; }

.callback--btn {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: transparent;
  border: none;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  text-align: right;
  letter-spacing: -0.2px;
  color: #1B1C21; }
  .callback--btn:not(:last-child) {
    margin-right: 30px; }
  .callback--btn .btn {
    margin-left: 8px; }

@media (min-width: 768px) {
  .hero {
    padding: 24px; }
  .hero__text-container {
    height: 160px;
    padding-right: 36px; }
  .hero__text {
    max-width: 50%;
    height: 100%; }
  .hero__main-logo {
    width: 50%;
    height: 260px;
    border-radius: 6px; }
  .hero__main-wrapper {
    display: flex;
    justify-content: space-between; }
  .hero__heading:before {
    left: -24px; } }

@media (min-width: 1088px) {
  .hero__callback-btn {
    display: flex;
    justify-content: space-between; }
  .hero__text {
    max-width: 50%;
    height: 100%; }
  .hero__main-logo {
    width: 50%;
    height: 307px; }
  .hero__main-wrapper {
    display: flex;
    justify-content: space-between; } }

@media (min-width: 1440px) {
  .hero {
    padding: 42px 30px 41px 34px; }
  .hero__nav-items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    overflow: visible; }
  .hero__heading:before {
    left: -34px; } }
